/* movie.css */

.movie{
  width:45%;
  background:#fff;
  padding:20px;
  box-shadow:0px 0px 10px #ccc;
  display:inline-block;
  margin-right:30px;
  margin-bottom:30px;
  position:relative;
}
@media screen and (max-width:1617px) {
  .movie{
    width:40%;
  }
}
@media screen and (max-width:916px) {
  .movie{
    width:90%;
  }
}
@media screen and (max-width:767px) {
  .movie{
    width:100%;
    margin:30px auto;
    padding:0px 2%;
    box-sizing: border-box;
  }
}
.movie img{
  float:left;
  max-width:150px;
  width:100%;
  position:relative;
  top:-30px;
  margin-right:30px;
  box-shadow:0px 20px 20px -12px #8f8f8f;
  transition:0.3s;
  perspective:200px;
}
.movie img:hover{
  transform:perspective(300px) rotateX(30deg);
}
.movie_data{}
.movie_title, .movie_year{
  font-weight:300px;
}
.movie_title{
  font-size:20px;
  margin-bottom:5px;
  color: #2c2c2c;
}
/* 장르 서식 */
.movie_genres{
  display:flex;
  flex-wrap:wrap;
  margin:5px 0px;
}
.movie_genres_txt{
  margin-right:10px;
  font-size:14px;
}
/* year서식 */
.movie_year{
  margin-right:10px;
  font-size:14px;
  color:#000000;
}


