/* App.css */

*{margin:0px;padding:0px;}
body{
  font-family: '맑은 고딕',arial, sans-serif;
  font-size:12px;
  color:#333;
  height:100%;
  background-color: #eff3f7;
}
ul, li{list-style:none;}
.load_img{
  position:fixed;
  top:50%;left:50%;
  transform: translate(-50%, -50%);
}
h1 img{padding-top:30px;}

main{
  padding-top:110px;
  padding-left:200px;
}

/* pc버전 */
@media screen and (min-width:768px) {
  .header{
    position:fixed;
    left:0px;top:0px;
    width:180px;height:100vh;
  }
  .header > h1{
    text-align:center;
    padding:15px 0px;
  }
}

/* 모바일 버전 */
@media screen and (max-width:767px) {
  .header{
    position:absolute;height:70px;
    width:100%;top:0px;left:0px;
    border-bottom:1px solid #ccc;
  }
  .header h1{
    text-align:center;
  }
  nav{display:none;}
}
/* 내비게이션 서식 */
.nav{
  padding-top:70px;
  text-align:center;  
}
.nav li{
  line-height:40px;
}
.nav a{
  text-decoration:none;
  font-size:16px;
  padding:5px 10px;
  color:#5d5d5d;
  text-shadow:0px 1px 1px #adadad;
}

footer{
  background:#333;
  color:#fff;
  text-align:center;
}
footer > address{
  line-height:50px;
  font-style:normal;
}